header{
    background-color: #fff;
    border-bottom:1px solid var(--bs-gray-300);
    padding: 0 5%;
    
}
.log>img{
    height: 70px;
}

.menu{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:start;
}
.menu>.user{
    margin-left: 15px;
    margin-right: 15px;
}
.menu-button>i{
    margin-left: 5%;
    font-size: 25px;
    font-weight: bold;
    color: var(--bs-gray-800);
    cursor: pointer;
}
.menu > .col>.name{
    color: var(--bs-gray-dark);
    font-weight: 800;
    
}
.menu > .col>.role{
    color: var(--bs-gray);
}
.menu>.settings>i{
    font-size: 25px;
    color: black;
    padding:0 12px;
}
.menu>.settings>i:hover{
    color: var(--bs-primary);
}

/*model*/
.model{
    
    min-width: 170px;
    padding: 15px;
    position: absolute;
    z-index: 101;
    top:70px;
    right: 10px;
    background-color: #fff;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
    animation-name: DropDownSlide;
    animation-duration: 0.3s;
    animation-fill-mode: both;
    font-size: 0.875rem;
}
.model>ul{
    list-style-type: none;
    margin: 0 !important;

}
.model>ul>li{
    padding: 10px;
}
.model>ul>li{
    display: flex;
    flex-direction: row;
    align-items: start;
    font-size: 16px;
}

.model>ul>li>div{
    margin-left: 10px;
}
.logout-text{
    margin-left: 15px;
}

.search{
    background-color: #f6f6f7;
    color: #343a40 ;
    padding: 10px;
}
.search>input{
    border: none !important;
    background:none !important;
}
.search>input:focus{
    border: none !important;
    background:none !important;
    outline: none;
}
.search>i{
    color: #ced4da;
}
.menu>.notification>i{
    font-size: 25px;
    color: black;
    
}
.menu>.notification>i:hover{
    color: var(--bs-primary);
}
.notification>sup{
    vertical-align: top;
}
