@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300;400;500;600&display=swap');
:root {
    --bs-blue: #5369f8;
    --bs-indigo: #564ab1;
    --bs-purple: #6f42c1;
    --bs-pink: #e83e8c;
    --bs-red: #ff5c75;
    --bs-orange: #f77e53;
    --bs-yellow: #ffbe0b;
    --bs-green: #43d39e;
    --bs-teal: #1ce1ac;
    --bs-cyan: #25c2e3;
    --bs-white: #fff;
    --bs-gray: #6c757d;
    --bs-gray-dark: #343a40;
    --bs-gray-100: #f8f9fa;
    --bs-gray-200: #f6f6f7;
    --bs-gray-300: #e2e7f1;
    --bs-gray-400: #ced4da;
    --bs-gray-500: #adb5bd;
    --bs-gray-600: #6c757d;
    --bs-gray-700: #4B4B5A;
    --bs-gray-800: #343a40;
    --bs-gray-900: #212529;
    --bs-primary: #5369f8;
    --bs-secondary: #6c757d;
    --bs-success: #43d39e;
    --bs-info: #25c2e3;
    --bs-warning: #ffbe0b;
    --bs-danger: #ff5c75;
    --bs-light: #f8f9fa;
    --bs-dark: #343a40;
    --bs-primary-rgb: 83, 105, 248;
    --bs-secondary-rgb: 108, 117, 125;
    --bs-success-rgb: 67, 211, 158;
    --bs-info-rgb: 37, 194, 227;
    --bs-warning-rgb: 255, 190, 11;
    --bs-danger-rgb: 255, 92, 117;
    --bs-light-rgb: 248, 249, 250;
    --bs-dark-rgb: 52, 58, 64;
    --bs-white-rgb: 255, 255, 255;
    --bs-black-rgb: 0, 0, 0;
    --bs-body-color-rgb: 75, 75, 90;
    --bs-body-bg-rgb: 243, 244, 247;
    --bs-font-sans-serif: "IBM Plex Sans", sans-serif;
    --bs-font-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    --bs-gradient: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0));
    --bs-body-font-family: IBM Plex Sans, sans-serif;
    --bs-body-font-size: 0.875rem;
    --bs-body-font-weight: 400;
    --bs-body-line-height: 1.5;
    --bs-body-color: #4B4B5A;
    --bs-body-bg: #f3f4f7;
    --nav-color:#6e7488;
}
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
  font-family: 'IBM Plex Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  
  
}
.page-title{
  font-size: 1.05rem;
  margin: 0;
}
.page-title-box{
  align-items: center;
  justify-content: space-between;
  display: flex;
  margin: 0 -27px 0;
  padding: 20px 27px;
}
.out-container{
  padding: 0 5%;
}
.col-6{
  width: 50% !important ;
}
.content{
  background-color:var(--bs-white) ;
  padding: 16px;
}
h5{
  font-size: 16px;
}
h6{
  font-size: 16px !important;
}
.form-control{
  background-color: #fff !important;
}
.form-select{
  background-color: #fff !important;
}

.edit{
  color: green;
}
.trash{
  color: red;
}
.card{
  background-color: var(--bs-white) !important;
  
  position: inherit !important;
}
.a-border{
  border: 1px solid #e2e7f1;
}

.css-b62m3t-container {
  padding: 0 !important;
}
/* For Webkit browsers (Chrome, Safari, newer versions of Opera) */
::-webkit-scrollbar {
    width: 5px;
    display: none; /* Width of the scrollbar */
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #888; /* Color of the scrollbar handle on hover */
  }
  
  /* Handle on active state */
  ::-webkit-scrollbar-thumb:active {
    background: #555; /* Color of the scrollbar handle on active state */
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #fff; /* Color of the scrollbar track */
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background:var(--bs-gray-500); /* Color of the scrollbar handle */
    border-radius: 100vw;
  }

  .waiting-model{
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    background-color: #21252944;
  }
.upload-container{
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: #21252971;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.drop-model{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 40vh;
}
.close{
  display: flex;
  position: absolute;
  z-index: 1;
  
  cursor: pointer;

}