.side-menu{
    padding: 0 24px;
    background-color: #fff;
    border-right:1px solid var(--bs-gray-300);
    border-bottom:1px solid var(--bs-gray-300);
    transition: transform 0.3s ease;
    padding: 0 5%;
    
}

  
  .side-menu.closed {
    transform: translateX(-100%);
  }
  
  .side-menu.open {
    transform: translateX(0);
  }
  

.item-container{
    overflow-x: auto;
    
}

.title{ 
    letter-spacing: 0.05em;
    cursor: pointer;
    /* font-size: 0.95rem !important; */
    text-transform: uppercase;
    color: var(--nav-color);
    position: relative;
}
.title>i{
    padding-right: 10px;
    font-weight: 800;
}
.item{
    padding: 20px 0;
    
}
.menuItem{
    display: flex;
    flex-direction: column !important;
    position: absolute;
    z-index: 11;
    top: 60px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    width: 200px;
}
.nav-link{
    text-decoration: none !important;
    color: var(--bs-gray-700) ;
    margin: 10px 0;
    font-weight: bold;
    font-size: 14px !important;
}
.nav-link:active{
    color: #5369f8 !important;
}
.nav-link>i{
    padding-right: 15px;
}


.link:hover{
    color: #5369f8 !important;
    
    
}
.menuItem{
    opacity: 1;
    transition: opacity 0.3s ease, max-height 0.3s ease;
}

.menu-item-enter {
    opacity: 0;
    max-height: 0;
  }
  
  .menu-item-enter-active {
    opacity: 1;
    max-height: 500px; /* Set a suitable max-height for your menu items */
    transition: opacity 0.3s ease, max-height 0.3s ease;
  }
  
  .menu-item-exit {
    opacity: 1;
    max-height: 500px; /* Set a suitable max-height for your menu items */
  }
  
  .menu-item-exit-active {
    opacity: 0;
    max-height: 0;
    transition: opacity 0.3s ease, max-height 0.3s ease;
  }
  
